import findByName from "../../util/findByName";

function mapList(...list) {
  return list.map(pkm => {
    const [index, name, stat] = pkm;
    return {
      index,
      name,
      stat,
    };
  });
}

export function findPokemonByName(name) {
  return findByName(pokedex, name);
}

